	module.exports = {"data":{"markdownRemark":{"html":"<p>Tocquevilleの「古いシステムとフランス革命」を早朝に読む。過去には、もう一つの重要な本「アメリカの民主主義」を見た。この本は彼の影響力のある本でもあり、読む価値があります。</p>\n<p>開発フォーラムは、X市副市長に経済発展についての報告を求める。ヤン大統領は会談を主宰し、復旦大学が彼を教授に招待したと語った。 X氏は、復旦大学の教授が副市長よりも栄誉を得ており、誰もが賞賛したと語った。彼はまた、反政府勢力にハイテクに投資する上場企業を見つけるために、誰もが賞賛したと語った。国家経済発展のような上海の状況における6つの新たな発展は、新しいレベル、改革と開放の新しい展開、浦東の新しい発展、産業構造の新たな発展などに達した。</p>\n<p>上海は今や財政的には地方政府の手に委ねられているが、それは10ポットと6つのカバーと呼ばれ、それをカバーする必要がある人はそれをカバーし、払い戻されず、去年もっと利益を上げることになる。上海の交通は良いではありません、私たちは、産業構造を変更する必要があります我々はもはや繊維産業と機械製造に従事することができないように、輸送能力を維持することはできません。主に金融業界において、第三次産業に従事するには、お金を運ぶのは簡単で軽い。彼はついにいくつかのボトルネックについて話しました。レポートは非​​常にエキサイティングで非常に魅力的です。</p>","id":"/home/swain/workspace/political-life/src/pages/ja/0013.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ja/0013.md","frontmatter":{"title":"1月21日 金曜日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ja/0013.md absPath of file >>> MarkdownRemark"}}

